.home {
    position: absolute;
    top: 1rem;
    left: 1rem;
    height: 2rem;
    width: 2rem;
    background-color: #000000;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.9);
    z-index: 999;
    animation: App-logo-spin infinite 4s linear;
}

@keyframes App-logo-spin {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(-5deg);
    }
    50% {
        transform: rotate(0deg);
    }
    75% {
        transform: rotate(5deg);
    }
    100% {
        transform: rotate(0deg);
    }
}
